import './App.css';
// import About from './Components/About';
import Navbar from './Components/Navbar';
import TextForm from './Components/TextForm';
import React, { useState } from 'react';
import Alert from './Components/Alert';
// import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
// import React from "react";
// import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

function App() {


  // by default it is always partial matching for link to ??>>>....

  const [alert, setalert] = useState(null);

  const showAlert  = (message, type) =>{
    setalert({
      msg: message,
      type: type
    })
  
  setTimeout(() => {
    setalert(null);
  }, 5000);
}

  const [text, setText] = useState("about_simple");

  const setAbout = () =>{
    setText("about_data");
  }
  let [mode, setMode] = useState("light"); //whether dark mode is enabled or not
  const toggleMode =() =>{
    if(mode==="light"){
      setMode('dark')
      // document.title='Textutils - Dark Mode';
      document.body.style.backgroundColor ='#042743';
      showAlert("dark mode has been enabled" ,"success")
      // setInterval(() => {
      // document.title='Textutils -Amazing';
      // }, 1000);
      // setInterval(() => {
      //   document.title='Textutils - not Amazing';
      //   }, 1500);
    }
    else {
      setMode('light')
      document.title='Textutils - light Mode';

      document.body.style.backgroundColor ='white';
      showAlert("light mode has been enabled" ,"warning")
  
    }
  }
 
  const green_mode = () =>{
    document.body.style.backgroundColor ='green';
    document.body.style.color ='light';
    showAlert("green_mode mode has been enabled" ,"warning")

    setMode('light')
  }

  const yellow_mode = () =>{
    document.body.style.backgroundColor ='#efce6b';
    document.body.style.color ='light';
    showAlert("yellow_mode mode has been enabled" ,"danger")


    setMode('info')
  }

  const red_mode = () =>{
    document.body.style.backgroundColor ='red';
    document.body.style.color ='light';
    showAlert("red_mode mode has been enabled" ,"success")


    setMode('secondary')
  }


  return (
    <>
        <div className="container">
    
     
      <Navbar title={"textUtils"}  setAbout={setAbout} text={text} mode={mode} toggleMode={toggleMode} red_mode={red_mode} yellow_mode={yellow_mode} green_mode={green_mode} />
        <div className="container mt-3">
        <Alert alert={alert} />
        </div>
      <div className="container mt-3">
      
          {/* <about exact path="/about" element={<About />} /> */}
          {/* <Route exact path="/" element={ */}
          <TextForm showAlert={showAlert}/>
          {/* } /> */}
      
      </div>
    
        
      
        </div>
    </>
  );
}

export default App;

