import React , {useState} from 'react'

export default function TextForm(props) {
let [text, setText] = useState("");

const handleUpClick = ()=>{
  var newText = text.toUpperCase();
  setText(newText);
  props.showAlert("converted to upper case" ,"danger")
  // console.log("the handle was clicked" + text);
}

const handleClear = ()=>{
  // var clear_text = text =(" ");
  setText(" ");
  props.showAlert("text cleared successfully" ,"dark")

  // setText(newText);
  // console.log("the handle was clicked" + text);
}

console.log(props.mode , "this is the color right now")


const handleCopy =() =>{
  var text = document.getElementById("myBox");
  text.select();
  navigator.clipboard.writeText(text.value);
  props.showAlert("text copied to clipboard successfully" ,"dark")

}

const handleLowClick = ()=>{
  
  var newText = text.toLowerCase();
  setText(newText);
  props.showAlert("converted to lower case" ,"light")
  // console.log("the handle was clicked" + text);
}

const handleExtraSpaces =() =>{
let newText = text.split(/[ ]+/);
setText(newText.join(" "))
props.showAlert("handled all extra spaces successfully" ,"primary")

}
// const [important , very_important] = useState("*");



const handleOnChange = (event)=>{
  // console.log("the handle on change");
  setText(event.target.value);

  // setText(event.target.value);  iski wjh se handleOnChange to kaam krega hi 
  // sath hi saath jo hum values daalege wo b add hoti rhegi
}
// it was not taking the value of setText because i was using const in setText dec..
// text="new data text "; this is a wrog way to set the text
//setText="new data text "; //this is the correct way to set the text

  return (
    <>
      <div style={{color: props.mode==='dark' ? '#042743' :  'white'}} className="container">
    <div className='container'>
        
<div class="mb-3">
    {/* <h1>{props.heading}{important}</h1> */}
  <textarea className={`form-control bg-${props.mode}`} id="myBox"  style={{backgroundColor: props.mode==='dark' ? 'pink' : 'green' ,
   color:props.mode==='dark' ? 'white' : '#042743'}} onChange={handleOnChange} value={text} rows="10"></textarea>
</div>
<button className={`btn btn-${props.mode==='dark'? 'white' : 'grey'}`} onClick={handleUpClick}>Covert to UPPERCASE</button>
<button className={`btn btn-${props.mode==='dark'? 'white' : 'grey'} ms-3`} onClick={handleLowClick}>Covert to lowercase</button>
<button className={`btn btn-${props.mode==='dark'? 'white' : 'grey'} ms-3`} onClick={handleClear}>Clear text-area</button>
<button className={`btn btn-${props.mode==='dark'? 'white' : 'grey'} ms-3`} onClick={handleCopy}>Copy text</button>
<button className={`btn btn-${props.mode==='dark'? 'white' : 'grey'} ms-3`} onClick={handleExtraSpaces}>Remove Extra Spaces</button>


    </div>

    <div className="container my-4" style={{color: props.mode==='dark'? 'white' : 'grey'}}>
      <h2>your text summary</h2>
      <p>{text.split(" ").length} words and {text.length} characters</p>
      <p>{0.008*text.split(" ").length} minutes needed to read the textarea</p>
      <h2>Preview</h2>
      <p>{text.length>0?text:"enter something to preview it"}</p>
    </div>
    </div>
    </>
  )
}


