import React from 'react';
import PropTypes from 'prop-types';
import PropTypesExact from 'prop-types-exact';
// import {Link } from "react-router-dom";


function Navbar(props) {


  // to doff between partial and exact matching use always exact path while aing

  // if (!props.title || !props.about_text) {
  //   throw new Error('Navbar component requires "title" and "about_text" props.');
  // }
  return (
    <nav className={`navbar navbar-expand-lg b navbar-${props.mode} bg-${props.mode} g-body-tertiary`}>
      <div className="container-fluid">
        <a className="navbar-brand" href="#">{props.title}</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <a className="nav-a active ms-2" aria-current="page" href="#">Home</a>
            </li>

            <li className="nav-item">
              <a className="nav-a ms-2" href="#">About</a>
            </li>
          </ul>

           
            <div className="btn btn-warning text-white rounded-circle border-2 p-3" onClick={props.yellow_mode}> Y </div>
            <div className="btn btn-danger text-white rounded-circle border-2 ms-2 p-3" onClick={props.red_mode}> R </div>
            <div className="btn btn-success text-white rounded-circle border-2 ms-2 p-3" onClick={props.green_mode}> G </div>
   
          <form className="d-flex" role="search">


          <div className={`form-check me-4 form-switch text-${props.mode==='light' ? 'dark': 'light'}`}>
          <input className="form-check-input" type="checkbox" onClick={props.toggleMode} role="switch" id="flexSwitchCheckDefault" />
          <label className="form-check-label " htmlFor="flexSwitchCheckDefault">switch checkbox</label>
            </div>
            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
            <button className="btn btn-success" type="submit">Search</button>
          </form>
        </div>
      </div>
    </nav> 
  );
}

Navbar.propTypes = PropTypesExact({
  title: PropTypes.string.isRequired,
  about_text: PropTypes.string.isRequired
});





export default Navbar;


